<script lang="ts">import { _ } from "svelte-i18n";
import { notify, request } from "./lib/notifications";
import { end } from "./lib/time";
;
export let value;
let done = false;
let endDate;
let remaining;
let interval;
function round(x) {
    return x.toFixed(0).padStart(2, "0");
}
function start() {
    request();
    endDate = end(value);
    remaining = value;
    interval = setInterval(() => {
        let diff = (endDate.getTime() - Date.now()) / 1000; // Seconds
        if (diff <= 0) {
            notifyy();
            done = true;
            stop();
        }
        else {
            const hours = Math.floor(diff / 3600);
            diff -= hours * 3600;
            const minutes = Math.floor(diff / 60) % 60;
            diff -= minutes * 60;
            const seconds = diff;
            remaining = { hours, minutes, seconds };
        }
    }, 1000);
}
function notifyy() {
    notify("Cukbuk :: " + $_("timer.title"), $_("timer.end"));
}
function stop() {
    endDate = null;
    clearInterval(interval);
}
</script>

{#if endDate}
    <span on:click|stopPropagation={stop}>
        ‚èπ {round(remaining.hours)}:{round(remaining.minutes)}:{round(
            remaining.seconds
        )}
    </span>
{:else}
    <span on:click|stopPropagation={start}>
        {#if done}‚úÖ{:else}‚ñ∂{/if}
        {round(value.hours)}:{round(value.minutes)}:{round(value.seconds)}
    </span>
{/if}

<style>
    span {
        cursor: pointer;
    }
</style>
